import { PremiumActions } from '@app/actions/premium';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export type Details = Record<string, string>;

export type PaymentStatusEventType = 'payment_status' | 'payment_started';

export type PaymentMessageEvent = {
  type: PaymentStatusEventType;
  details?: Details | null;
};

const handleMessage = async (event: MessageEvent<PaymentMessageEvent>) => {
  try {
    if (!event.data || typeof event.data !== 'object') return;

    if (event.data.type === 'payment_started') {
      await browser_api.runtime.sendMessage({
        action: PremiumActions.PaymentStarted,
        payload: { details: event.data.details || null },
      });
    }

    if (event.data.type === 'payment_status') {
      handlePaymentStatusReceived();
    }
  } catch (error) {
    console.error(error);
  }
};

const handlePaymentStatusReceived = async () => {
  const queryParams = new URLSearchParams(window.location.search);
  const app = queryParams.get('app');

  if (window.location.pathname.includes('payment') && app === 'ub') {
    const { userId } = await getLocalStorageData(LocalStorageKey.UserId);
    const userIdElementContainer = document.getElementById('user-id');

    if (!userId || !userIdElementContainer) {
      return;
    }

    const userText = document.createElement('span');
    userText.style.fontStyle = 'italic';

    userText.innerHTML =
      '<br />     5 .<br />         Premium,   ID  ,  "  ID".<br /><br />';

    const userIdElement = document.createElement('span');

    userIdElement.innerText = ` ID - ${userId}`;
    userIdElement.style.fontWeight = 'bold';
    userIdElement.style.fontSize = '20px';
    userIdElement.style.color = 'var(--highlighted-text-color)';

    userIdElementContainer.appendChild(userText);
    userIdElementContainer.appendChild(userIdElement);
  }
};

window.addEventListener('message', handleMessage, false);
